div {
  font-family: "Press Start 2P", serif;
}

small, h5, button, .footer {
  font-size: inherit;
}

.Footer {
  padding-top: 90px;
  padding-bottom: 0;
  font-size: 10px;
}

h1 {
  text-align: center;
  text-shadow: 2px 2px 5px #fff420;
  margin-top: 30px;
  padding: 10px;
  font-family: "Press Start 2P", serif;
  font-weight: bolder;
  font-size: 30px !important;
}

.city {
  padding-top: 40px;
}

.H-W {
  text-align: center;
  font-family: "Press Start 2P", serif;
  font-size: 10px;
}

.day {
  text-align: center;
  opacity: .6;
  color: #ed92cd;
  margin-top: -10px;
  margin-bottom: 30px;
  font-family: "Press Start 2P", serif;
  font-size: 10px !important;
}

.desc {
  text-align: center;
  margin-top: -15px;
  font-size: 15px;
  font-weight: bold;
}

hr {
  margin-top: 10px;
}

img {
  margin: 0 auto;
  display: block;
}

.city-input {
  background-color: #fff;
  border: 2px solid #000;
  border-top: 3px solid #fff;
  border-left: 3px solid #fff;
  padding-bottom: 14px;
  font-family: "Press Start 2P", serif;
  font-size: 10px !important;
}

.submit {
  background-color: #ff74d1;
  border: 2px solid #000;
  border-top: 3px solid #fff;
  border-left: 3px solid #fff;
  padding-bottom: 14px;
  font-size: 10px !important;
}

.weather-app {
  background: #fff7fb;
  border: 2px solid #ff7cc4;
  border-radius: 15px;
  max-width: 500px;
  height: 700px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 5px 10px #ff98d8;
}

.button {
  background-color: #1dff37;
  border: 2px solid #000;
  margin-left: -20px;
}

.button:hover {
  color: #ffd118;
}

.float-left {
  color: #000;
  text-align: center;
  padding-bottom: 20px;
  font-weight: bold;
}

.temp {
  text-align: center;
  color: #000;
  text-shadow: 2px 2px 5px #fffa72;
  margin-right: 30px;
  font-size: 80px;
  display: block;
}

.weather-forecast {
  text-align: center;
  background: #94e1ff;
  border: 2px solid #fffbfb;
  border-radius: 15px;
  width: 300px;
  height: 400px;
  margin-top: -350px;
  margin-left: 408px;
}

.weather-forecast-temperatures {
  color: #252624;
  font-size: 20px;
}

.weather-forecast-temperature-min {
  opacity: .6;
}

small:hover {
  color: #513eff;
  font-weight: thin;
}

small a {
  color: #000;
}

h5 {
  text-align: left;
  color: #fff;
  font-size: 5px;
}

a:hover {
  color: #ffb310;
}

.unit {
  font-size: 15px;
}

.icons {
  text-align: center;
  color: #000;
}
